@font-face {
    font-family: "Agency FB Bold";
    src: url(AGENCYB.TTF) format("truetype");
}

h1,h2,h3,h4,h5,h6 {
    font-family: "Agency FB Bold";
}

.page-header {
    margin: 10px 0 0 0;
    padding: 0 0 0 0;
    font-size: 40px;
    font-family: "Agency FB Bold";
}
.sub-header {
    font-size: 24px;
    margin: 0 0 0 0;
    padding: 0 0 0 0;
    font-family: "Agency FB Bold";
}

.body-text {
    font-size: 16px;
}

.black-white-link {
    text-decoration: none;
    color: black;
}
.black-white-link:hover {
    text-decoration: none;
    color: white;
}
