
.DefaultLayout {
    min-height: 100vh;
    background: black;
    color: white;
}

.page-content {
    font-family: Helvetica, Arial, sans-serif;
    padding-left: 25px;
    justify-self: center;
    text-align: left;
    flex-grow: 1;
}


.site-header {
    padding-top: 10px;
    background-color: #00B0F0;
    color: black;
}

.site-footer {
    padding-top: 1vh;
    padding-bottom: 1vh;
    font-family: Helvetica, Arial, sans-serif;
    text-align: center;
}

.cahaba-logo {
    margin-left: 25px;
    font-size: 15vh; /* Logo arguably should scale with screen size */
    font-family: "Agency FB Bold";
    flex-grow: 1;
}
.cahaba-logo a {
    color: inherit;
    text-decoration: inherit;
}



.menu-container {
    padding: 0px;
}

.menu-box {
    font-family: "Agency FB Bold", sans-serif;
    font-size: 32px;
    display: block;
    padding: 0px;
    margin-left: 10px;
    margin-right: 10px;
}

.sponsor-header {
    margin: 0 0 0 25px;
    padding: 0 0 0 0;
    font-size: 32px;
}

.sponsor-box {
    display: block;
    margin: 0 0 0 25px;
    padding: 0 0 0 0;
}
.sponsor-image {
    height: 75px;
    padding: 0px;
    border: 1px solid black;
}


td, th {
    border: 1px solid white;
}

